<template>
    <div class="tabbar">
      <p @click="onBridgeReady">微信支付来了啊啊啊啊啊啊啊啊啊啊</p>
      <div>
        <h1>get请求</h1>
        <p>{{goodsdate}}</p>
      </div>
      <div>
        <h1>post请求，跨域</h1>
        <p>{{postdata}}</p>
      </div>
      <div>
        <h1>本地json请求</h1>
        <p>{{goodslist}}</p>
      </div>

      <div>
        <p @click='jiazai'>点击加载</p>
      </div>
    </div>
</template>

<script>
  import VDistpicker from 'v-distpicker'
  import { MessageBox } from 'mint-ui';
  import { Toast } from 'mint-ui';
  import { Indicator } from 'mint-ui';
export default {
name: '',

  data() {
    return {
      selected: 0,
      goodsdate:[],
      popupVisible: false,
      postdata: '',
      goodslist: ''
    }
  },
    created() {
        // get
        let url = 'https://autumnfish.cn/api/joke';
        this.$http.get(url)
        .then(res=>{
          console.log(res.data)
          this.goodsdate=res.data
        })
        .catch(function(err){
          // console.log(err)
        })

        // post
        const postdata={
          key : '12'
        }
        this.$http.post('kua/openapi/api',postdata).then((res)=>{
          console.log(res.data)
          this.postdata=res.data.text
        })

        // 本地json
        let url2 = 'static/data/goodsList.json';
        console.log(url)
        // this.$http.get("src/data/courseList.data")
        this.$http.get(url2)
        .then(res=>{
          console.log(res)
          console.log(res.data.url_da)
          this.goodslist=res.data[0].title
        })
        .catch(function(err){
          console.log(err)
        })
  },
  methods:{
    handleClick: function () {
        this.popupVisible = true
    },
    onBridgeReady(){
      window.WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId":"wx2421b1c4370ec43b",     //公众号名称，由商户传入     
            "timeStamp":"1395712654",         //时间戳，自1970年以来的秒数     
            "nonceStr":"e61463f8efa94090b1f366cccfbbb444", //随机串     
            "package":"prepay_id=u802345jgfjsdfgsdg888",     
            "signType":"MD5",         //微信签名方式：     
            "paySign":"70EA570631E4BB79628FBCA90534C63FF7FADD89" //微信签名 
          },
          function(res){
          if(res.err_msg == "get_brand_wcpay_request:ok" ){
          // 使用以上方式判断前端返回,微信团队郑重提示：
                //res.err_msg将在用户支付成功后返回ok，但并不保证它绝对可靠。
          } 
      }); 
    },
    jiazai(){
      Indicator.open({
        text: '加载中...',
        spinnerType: 'fading-circle'
      });
    }

  },
  components: {
      child: {
        template: `
          <div>
            <div>kuangzi</div>
          <div>kuangzi</div>
          
          </div>
        
        `
      }
    }
}
</script>

<style scoped>

</style>



